<template>
<v-layout row wrap>
<v-flex xs8 offset-xs2>
    <v-expansion-panel
      v-model="groups"
      expand
    >
      <v-expansion-panel-content
        v-for="item in commands"
        :key="item.title"
      >
        <template v-slot:header>
          <div>#Category: {{ item.title }}</div>
        </template>
        <v-card dark>
          <v-card-text class="blue lighten-5">

              <v-expansion-panel
                  v-model="item.model"
                  expand
                >
                  <v-expansion-panel-content
                    v-for="com in item.commands"
                    :key="com.title"
                  >
                    <template v-slot:header>
                      <div>#{{ com.title }}</div>
                    </template>
                    <v-card dark>
                      <v-card-text class="blue lighten-0">

                          <br><h3 class="headline mb-0">{{ com.info ? `#Info: ${com.info}` : '' }}</h3>
                          <br><h3 class="headline mb-0">{{ com.usage ? `#Usage: ${FixPrefix(com.usage)}` : '' }}</h3>
                          <br><h3 class="headline mb-0">{{ com.description ? `#Description: ${FixPrefix(com.description)}` : '' }}</h3>

                      </v-card-text>
                    </v-card>
                  </v-expansion-panel-content>
                </v-expansion-panel>

          </v-card-text>
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
</v-flex>
</v-layout>
</template>

<script>
import { commands } from '../plugins/commands'
const prefix = '>'

export default {
  name: 'commands',
  data: () => ({
    commands: commands[2].commands,
    groups: [],
    command: [],
    prefix
  }),
  methods: {
    FixPrefix (prefix) {
      return prefix.split('<prefix>').join(this.prefix)
    }
  }
}
</script>

<style>
.theme--dark.v-expansion-panel .v-expansion-panel__container {
  background-color: #9c27b0 !important
}
</style>
